import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
  },
  {
    path: '/front',
    name: 'Front',
    component: () => import('../views/Front.vue'),
    children: [
      {
        path: 'onlinestore',
        name: 'Onlinestore',
        chinese: '線上購物',
        component: () => import('../views/front/Onlinestore.vue'),
        onNav: true,
        children: [
          {
            path: ':category',
            name: 'Category',
            component: () => import('../views/front/Category.vue'),
          },
          {
            path: ':category/:id',
            name: 'Product',
            component: () => import('../views/front/Product.vue'),
          },
        ],
      },
      {
        path: 'news',
        name: 'News',
        chinese: '最新消息',
        component: () => import('../views/front/News.vue'),
        onNav: true,
      },
      {
        path: 'about',
        name: 'About',
        chinese: '關於我們',
        component: () => import('../views/front/About.vue'),
        onNav: true,
      },
      {
        path: 'orderinfo',
        name: 'Orderinfo',
        chinese: '購物指南',
        component: () => import('../views/front/Orderinfo.vue'),
        onNav: true,
      },
      {
        path: 'contact',
        name: 'Contact',
        chinese: '聯絡我們',
        component: () => import('../views/front/Contact.vue'),
        onNav: true,
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
